.dark{
    --bg: #1f293c;
    --text: white;
    --hlcolor: gray;
    --credits: gray;
    --line: gray;
  }
  
  .light{
    --bg: white;
    --text: #282c34;
    --line: black;
    --hlcolor: gray;
    --credits: black;
  }
  
  .fire{
    
    --bg: linear-gradient(45deg, blue, red);
    --text: white;
    --filter: blur(10px);
    --hlcolor: red;
    --credits: white;
    --line: white;
    }
  
  
  
  html {
  background-color: var(--bg);
