    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    font-size: calc(10px + 2vmin);
    color: var(--text);
    cursor: cell;
    margin-left: 2%;
    width: 100%;
  }	
  
  
  
  
  
