  /*language-changer https://codepen.io/himalayasingh/pen/EdVzNL */
  #app-cover
  {
    position:absolute;
    margin-top: 10px;
    margin-left: 15px;
    counter-reset: button-counter;
  }
  
  .button
  {
      position: relative;
      top: 50%;
      width: 74px;
      height: 36px;
      margin: -20px auto 0 auto;
  
  }
  
  
  
  .checkbox
  {
      position: relative;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      opacity: 0;
      cursor: pointer;
      z-index: 3;
  }
  
  
  .layer
  {
      width: 100%;
      background-color: transparent;
      transition: 0.5s ease all;
      z-index: 1;
  }
  
  /* language switch */
  #button-1 .knobs:before
  {
      content: '';
      position: absolute;
      top: 4px;
      left: 4px;
      width: 10px;
      font-size: 10px;
      font-weight: bold;
      text-align: center;
      line-height: 1;
      padding: 9px 4px;
      background: linear-gradient(180deg, transparent, transparent 4px, #c8102e 4px, #c8102e 7px, transparent 7px, transparent 100%), linear-gradient(90deg, transparent, transparent 10px, #c8102e 10px, #c8102e 12px, transparent 12px, transparent 100%), linear-gradient(180deg, transparent, transparent 4px, #FFF 4px, #FFF 7px, transparent 7px, transparent 100%), linear-gradient(90deg, transparent, transparent 9px, #FFF 9px, #FFF 13px, transparent 13px, transparent 100%), linear-gradient(26.56505deg, transparent, transparent 9px, #c8102e 9px, #c8102e 10px, transparent 10px, transparent 100%) 0 0/11px 100% no-repeat, linear-gradient(-26.56505deg, transparent, transparent 10px, #c8102e 10px, #c8102e 11px, transparent 11px, transparent 100%) 11px 0/11px 100% no-repeat, linear-gradient(26.56505deg, transparent, transparent 10px, #c8102e 10px, #c8102e 11px, transparent 11px, transparent 100%) 11px 6px/11px 100% no-repeat, linear-gradient(-26.56505deg, transparent, transparent 9px, #c8102e 9px, #c8102e 10px, transparent 10px, transparent 100%) 0 55px/11px 100% no-repeat, linear-gradient(27deg, transparent, transparent 9px, #FFF 9px, #FFF 11px, transparent 11px, transparent 100%), linear-gradient(153deg, transparent, transparent 9px, #FFF 9px, #FFF 11px, transparent 11px, transparent 100%), #012169;
  
      border-radius: 40%;
      transition: 0.5s cubic-bezier(0.18, 0.89, 0.35, 1.15) all;
  }
  
  #button-1 .checkbox:checked + .knobs:before
  {
      content: '';
      left: 30px;
      width: 10px;
      position: absolute;
      background-image: linear-gradient(black,red, yellow);
  
    }
  
  
  
  #button-1 .knobs, #button-1 .knobs:before, #button-1 .layer
  {
      transition: 0.5s ease all;
  }
  
  
  
  
  
